import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  Optional,
  Output,
  Self,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS, MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { NgClass, NgForOf, NgIf } from '@angular/common';
import { FormControl, FormGroupDirective, FormsModule, NgControl, NgForm, ReactiveFormsModule } from '@angular/forms';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatSelect, MatSelectModule } from '@angular/material/select';
import { MatOption, MatOptionSelectionChange } from '@angular/material/core';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { A11yModule } from '@angular/cdk/a11y';

import { BaseComponent } from 'sidsc-components/core';
import { DscInputComponent } from 'sidsc-components/dsc-input';

import { Option } from './shared/option';
import { DscTooltipModule } from 'sidsc-components/dsc-tooltip';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { DscCheckboxComponent } from 'sidsc-components/dsc-checkbox';


@Component({
  selector: 'dsc-select',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [
    MatButtonModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatTooltipModule,
    DscTooltipModule,
    NgIf,
    NgClass,
    ReactiveFormsModule,
    MatSelectModule,
    FormsModule,
    NgForOf,
    DscInputComponent,
    A11yModule,
    MatCheckboxModule,
    DscCheckboxComponent,
    MatDividerModule
  ],
  templateUrl: './dsc-select.component.html',
  styleUrls: ['./dsc-select.component.scss'],
  providers: [
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'outline' } }
  ]
})
export class DscSelectComponent extends BaseComponent<any> implements AfterViewInit {
  @ViewChild(MatSelect, { static: true })
  selectElement!: MatSelect;

  @Input()
  iconPrefix?: string;

  @Input()
  options?: Option[];

  @Input()
  compareWith: (o1: any, o2: any) => boolean = (o1: any, o2: any) => JSON.stringify(o1) === JSON.stringify(o2);

  @Input()
  get multiple(): boolean {
    return this._multiple;
  }

  set multiple(value: boolean | string) {
    this._multiple = coerceBooleanProperty(value);
  }

  _multiple = false;

  @Input()
  get showFilter(): boolean {
    return this._showFilter;
  }

  set showFilter(value: boolean | string) {
    this._showFilter = coerceBooleanProperty(value);
  }

  _showFilter = false;

  isActive: boolean = false;

  allSelected: boolean = false;

  isIndeterminate: boolean = false;

  @Input()
  value: any;

  override writeValue(value: any): void {
    this.value = value;
  }

  @Output()
  openedChange: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  selectionChange: EventEmitter<any> = new EventEmitter<any>();

  search: FormControl = new FormControl('');

  constructor(@Optional() @Self() public ngControl: NgControl,
              @Optional() parentForm: NgForm,
              @Optional() parentFormGroup: FormGroupDirective
  ) {
    super(ngControl, parentForm, parentFormGroup);
    if (ngControl) ngControl.valueAccessor = this;
  }

  ngAfterViewInit() {
    if (this.readOnly) {
      this.disableSelectOpening();
    }
  }

  private disableSelectOpening() {
    this.selectElement._keyManager.withHorizontalOrientation(null);

    this.selectElement._handleKeydown = (event: KeyboardEvent) => {
      if ([' ', 'Enter', 'ArrowDown', 'ArrowUp', 'Spacebar'].includes(event.key)) {
        event.preventDefault();
        event.stopPropagation();
        return;
      }
      this.selectElement._keyManager.onKeydown(event);
    };
  }

  filter(option: Option): boolean {
    const searchValue = this.search.value?.trim().toLowerCase() || '';
    const optionLabel = option.label?.trim().toLowerCase() || '';
    return optionLabel.includes(searchValue);
  }

  onOpenedChange(): void {
    this.search.setValue('');
    if (this._showFilter && this.selectElement.panel) {
      this.selectElement.panel.nativeElement.scrollTop = 0;
    }
  }

  change($event: MatOptionSelectionChange): void {
    if (this.multiple && $event.isUserInput) {
      const option = $event.source;
      const wasSelected = this._isSelected(option);
      if (wasSelected !== option.selected) option.selected ? this._select(option) : this._deselect(option);
    }
  }

  toggleAllSelections(selectAll: boolean): void {
    if (this.options) {
      const allValues = this.options
        .flatMap(option => option.options ? option.options.map(child => child.value) : option.value)
        .filter(value => value !== undefined);

      this.value = selectAll ? allValues : [];
      this.selectElement.options.forEach(option => selectAll ? option.select() : option.deselect());
      this.allSelected = selectAll;
      this.isIndeterminate = false;
    }
  }

  checkAllSelected(): void {
    if (this.options && this.selectElement) {
      const allValues = this.options
        .flatMap(option => option.options ? option.options.map(child => child.value) : option.value)
        .filter(value => value !== undefined);

      const allSelected = allValues.every(value => this.value?.includes(value));
      const someSelected = allValues.some(value => this.value?.includes(value));

      this.allSelected = allSelected;
      this.isIndeterminate = someSelected && !allSelected;
    }
  }

  private _isSelected(option: MatOption): boolean {
    return this.selectElement._selectionModel.selected.some(
      (o: MatOption) => JSON.stringify(o.value) === JSON.stringify(option.value)
    );
  }

  private _select(option: MatOption): void {
    const values = this.value;
    const optionSelected = option.value;
    this.value = values ? [...values, optionSelected] : [optionSelected];
    this.checkAllSelected();
  }

  private _deselect(option: MatOption): void {
    const values = this.value;
    this.value = this._unmarkSelected(option, values);
    this.checkAllSelected();
  }

  private _unmarkSelected(option: MatOption, values: Option[]): Option[] {
    let options: Option[] = [];
    if (this._isSelected(option)) options = values.filter((o: Option) => JSON.stringify(o) !== JSON.stringify(option.value));
    return options;
  }

  getTruncatedSelection(selectedItems: any[]): string {
    return selectedItems.map(item => item.viewValue).join(', ');
  }

  get formFieldClasses(): string[] {
    return [
      this.ngControl && this.ngControl.value?.length
        ? 'mat-mdc-form-field--filled'
        : 'mat-mdc-form-field--empty',
      this.readOnly ? 'mat-mdc-form-field--readonly' : '',
      this.ngControl &&
      this.ngControl.invalid &&
      (this.ngControl.touched || this.ngControl.dirty) &&
      !this.readOnly
        ? 'error-field'
        : '',
        (this.isActive && !this.errorText) || (this.ngControl && this.ngControl.value?.length)
        ? 'active-field'
        : ''
    ].filter(Boolean);
  }
}
